/* global styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container-medium {
  max-width: 1262px;
  margin: 0 auto;
}

.container-small {
  max-width: 1040px;
  margin: 0 auto;
}

.body {
  width: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content; /*header main footer*/
}
.body--scrolloff {
  overflow: hidden;
}
.body--menu-open {
  overflow: hidden;
}
.body--menu-open::before {
  content: "";
  z-index: 1;
  position: fixed;
  width: 100%;
  max-width: 100vw;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
}

.heading {
  font-family: var(--heading-font-family);
  font-weight: var(--heading-basic-font-weight);
}
.heading--large {
  font-size: var(--heading-large-font-size);
  line-height: var(--heading-large-line-height);
}
.heading--medium {
  font-size: var(--heading-medium-font-size);
  line-height: var(--heading-medium-line-height);
}
.heading--small {
  font-size: var(--heading-small-font-size);
  line-height: var(--heading-small-line-height);
}

.paragraph {
  font-family: var(--paragraph-font-family);
  font-weight: var(--paragraph-basic-font-weight);
}
.paragraph--large {
  font-size: var(--paragraph-large-font-size);
  line-height: var(--paragraph-large-line-height);
}
.paragraph--medium {
  font-size: var(--paragraph-medium-font-size);
  line-height: var(--paragraph-medium-line-height);
}
.paragraph--small {
  font-size: var(--paragraph-small-font-size);
  line-height: var(--paragraph-small-line-height);
}

/* variables */
:root {
  --standart-side-padding: 20px;
}

:root {
  --white: #FFFFFF;
  --light: #F5F0EC;
  --tumbleweed: #DCC1AB;
  --green: #1B5B31;
  --dark: #111111;
}

:root {
  --heading-font-family: "Montserrat", "arial", sans-serif;
  --paragraph-font-family: "Inter", "arial", sans-serif;
  --heading-basic-font-weight: 500;
  --heading-large-font-size: 60px;
  --heading-large-line-height: 70px;
  --heading-medium-font-size: 48px;
  --heading-medium-line-height: 55px;
  --heading-small-font-size: 28px;
  --heading-small-line-height: 32px;
  --paragraph-basic-font-weight: 400;
  --paragraph-large-font-size: 16px;
  --paragraph-large-line-height: 24px;
  --paragraph-medium-font-size: 14px;
  --paragraph-medium-line-height: 21px;
  --paragraph-small-font-size: 12px;
  --paragraph-small-line-height: 18px;
}

/* components */
.header {
  position: relative;
  width: 100%;
  background-color: var(--white);
  padding: 0 var(--standart-side-padding);
  z-index: 2;
}

.header__wrap {
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: center;
  padding: 24px 0;
}

.header__title {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 115px;
  height: 20px;
  z-index: 1;
}

.header__logo-icon {
  width: 115px;
  height: 20px;
}

.nav-button {
  justify-self: flex-end;
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  display: none;
}
@media (max-width: 900px) {
  .nav-button {
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.nav-button__burger {
  cursor: pointer;
  transition: transform 400ms;
  user-select: none;
}
.nav-button__burger--active {
  transform: rotate(45deg);
}
.nav-button__burger--active > .nav-button__burger-line--top {
  stroke-dasharray: 17 82;
  stroke-dashoffset: -62px;
}
.nav-button__burger--active > .nav-button__burger-line--middle {
  stroke-dashoffset: 23px;
}
.nav-button__burger--active > .nav-button__burger-line--bottom {
  stroke-dashoffset: -83px;
}

.nav-button__burger-line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #000000;
  stroke-width: 5.5;
  stroke-linecap: round;
}

.nav-button__burger-line--top {
  stroke-dasharray: 40 82;
}

.nav-button__burger-line--middle {
  stroke-dasharray: 40 111;
}

.nav-button__burger-line--bottom {
  stroke-dasharray: 40 161;
}

.nav {
  justify-self: flex-end;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 48px;
}
@media (max-width: 900px) {
  .nav {
    position: absolute;
    left: 0;
    right: 0;
    top: -350%;
    transition: all 0.5s ease-in;
    flex-direction: column;
  }
}
.nav--open {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 40px 20px;
  transition: all 0.5s ease;
  justify-content: center;
  background-color: var(--white);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
}
.nav--open .nav__list {
  pointer-events: all;
  opacity: 1;
  transition: all 0.5s ease;
}
.nav--open .nav__search {
  pointer-events: all;
  opacity: 1;
  transition: all 0.5s ease;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 48px;
  list-style: none;
}
@media (max-width: 900px) {
  .nav__list {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease;
  }
}

.nav__link {
  color: var(--dark);
  text-decoration: none;
}

@media (max-width: 900px) {
  .nav__search {
    pointer-events: none;
    opacity: 0;
  }
}

.search {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all ease 0.4s;
}
.search:hover > .search__input, .search:focus-within > .search__input {
  opacity: 1;
  width: 160px;
}

.search__input {
  height: 100%;
  width: 0px;
  pointer-events: 0;
  opacity: 0;
  padding: 5px 8px;
  transition: all ease 0.6s;
  border: 1px solid var(--dark);
  border-radius: 5px;
}
@media (max-width: 900px) {
  .search__input {
    width: 160px;
    opacity: 1;
    pointer-events: all;
  }
}

.search__icon {
  width: 24px;
  height: 24px;
  fill: var(--dark);
}

.nav-with-submenu {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.nav-with-submenu:hover > .nav-with-submenu__list, .nav-with-submenu:focus-within > .nav-with-submenu__list {
  top: 100%;
  opacity: 1;
  pointer-events: all;
}

.nav-with-submenu__button {
  align-self: stretch;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background-color: transparent;
}

.nav-with-submenu__icon {
  width: 12px;
  height: 12px;
  cursor: pointer;
}

.nav-with-submenu__list {
  position: absolute;
  top: -100%;
  left: 0;
  opacity: 0;
  pointer-events: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  gap: 5px;
  padding: 10px 0;
  background-color: var(--white);
  transition: all ease 0.4s;
  list-style: none;
}

.nav-with-submenu__item {
  display: flex;
}